import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  tabIndex = 1;
  tabChaged(index){
  	console.log(index);
  	// 让被绑定的tabIndex等于当前点击的下标
  	this.tabIndex = index;//此处的this表示当前组件
  }
}



