import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-query-money',
  templateUrl: './query-money.component.html',
  styleUrls: ['./query-money.component.css']
})
export class QueryMoneyComponent implements OnInit {
	tabIndex = 1;
  tabChaged(index){
  	console.log(index);
  	// 让被绑定的tabIndex等于当前点击的下标
  	this.tabIndex = index;//此处的this表示当前组件
  }

  constructor() { }

  ngOnInit() {
  }

}



